.auth-wrapper {
  margin: 15vh auto 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.filter-component {
  margin: 0 0 5vh 0;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  text-align: left;
}

.auth-inner {
  font-size: 1rem;
  margin: auto;
  width: 35vw;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 50px 40px 50px;
  border-radius: 15px;
  transition: all .3s;
}

@media only screen and (max-device-width: 600px) {
  .auth-inner {
    font-size: 1rem;
    margin: auto;
    width: 65vw;
    background: #ffffff;
    box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
    padding: 40px 50px 40px 50px;
    border-radius: 15px;
    transition: all .3s;
  }
}

/* mobile only styles when the device is 0-600px in maximum width */

@media only screen and (max-device-width: 1080px) {
  .auth-inner {
    font-size: 1rem;
    margin: auto;
    width: 85vw;
    background: #ffffff;
    box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
    padding: 40px 50px 40px 50px;
    border-radius: 15px;
    transition: all .3s;
  }
}

/* mobile only styles when the device is up to 1000px in maximum width */

.board-inner {
  /* border:50px;
